import { convertIntoRequiredFormat } from "./convertIntoRequiredFormat";
import * as XLSX from "xlsx";
import { s2ab } from "./s2ab";

export const mergeData = (data) => {
  const newData = data
    .map((el) => convertIntoRequiredFormat(el))
    .sort((a, b) => a.date - b.date) // Sort by date from past to future
    .map((item) => item.data);

  // Create a new workbook
  const newWorkbook = XLSX.utils.book_new();

  // Convert the updated data to a worksheet
  const newWorksheet = XLSX.utils.aoa_to_sheet(newData);

  // Add the new worksheet to the workbook
  XLSX.utils.book_append_sheet(newWorkbook, newWorksheet, "MergedData");

  // Generate a new Excel file
  const newExcelFile = XLSX.write(newWorkbook, {
    bookType: "xlsx",
    type: "binary",
    codepage: 65001,
  });
  const blob = new Blob([s2ab(newExcelFile)], { type: "" });
  const url = window.URL.createObjectURL(blob);

  // Create a download link
  const a = document.createElement("a");
  a.href = url;
  a.download = "updated_excel.xlsx";
  document.body.appendChild(a);
  a.click();
  document.body.removeChild(a);
};
